* {
  
  /* Main Color Palette */
  --ku-blue: #0051ba;
  --ku-crimson:#e8000d;
  --ku-jayhawk-yellow: #ffc82d;
  --ku-signature-grey:#85898a;
  
  /* Secondary Color Palette */
  --night: #003459;
  --lake: #2767ff;
  --sky: #73cbf2;
  --fog: #8e9fbc;
  --steam: #dde5ed;
  --brick: #971b2f;
  --fire: #ff3042;
  --wheat: #f2a900;
  --terra-cotta: #c66e4e;
  --limestone: #d7d2cb;
}


html {
  font-size: 62.5%;
}

body {
  color: #000;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1.6rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  align-items: center;
  background-color: var(--ku-signature-grey);
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

h1 {
  color: var(--ku-crimson);
}

nav {
  display: flex;
  justify-content: space-between;
  height: 100%;
}

nav ul {
  list-style-type: none;
  margin: 0;
}

nav ul>li {
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
}

nav ul>li a {
  background-color: var(--ku-blue);
  font-weight: 700;
  padding: 1rem 1rem 0 1rem;
  text-decoration: none;
}

nav ul>li a:hover {
  background-color: #fff;
  color: var(--ku-blue);
  text-decoration: none;
}
input {
  display: block;
  margin: 1rem;
}


#root {
  height: 100vh;
  width: 100vw;
}

button {
  cursor: pointer;
}

main {
  padding: 1rem;
}

h1 {
  font-size: 3rem;
}

h2,
h3,
h4,
h5,
h6 {
  color: var(--ku-crimson);
}

h2 {
  font-size: 2.75rem;
  text-align: center;
}

a {
  color: var(--ku-jayhawk-yellow);
  text-decoration: none;
}

a:hover {
  color: var(--ku-jayhawk-yellow);
  text-decoration: underline;
}

.testQuestion {
  border: 1px solid #000;
  border-radius: 1.5rem;
  color: #fff;
  display: flex;
  flex-flow: column;
  padding: 1rem;
  margin: 0 auto;
  width: 60%;
}

.testQuestion h3 {
  color: #fff;
}

.testAnswers form {
  display: flex;
  flex-flow: column;
  width: 100%;
}

.testAnswers ol {
  align-self: flex-start;
  list-style-type: upper-alpha;
  width: 100%;
}

.testAnswers input[type='submit'] {
  align-self: center;
  background-color: var(--ku-blue);
  border: none;
  border-radius: 1.25rem;
  color: #fff;
  font-size: 2rem;
  padding: 1.25rem 2rem;
  width: 100%;
}

